.scan-process{
    padding-top: 24px;
    padding-bottom: 76px;
 
 
    &__container{
        margin: 0 auto;
        padding-left:20px;  
        padding-right:20px;
         
    }
 
    &__title{
        margin-bottom: 16px;
        font-family: var(--main-font);
        font-weight: 700;
        font-size: 18px;
        line-height: 1.27;
        text-transform: uppercase;
 
        color: var(--blue);
    }
 
    &__list{
        list-style: none;
        display: flex;        
        flex-wrap: wrap;
        padding-bottom: 52px; 
        margin-left: -14px;
        margin-top: -14px;
    }
 
    &__item{
        box-sizing: border-box;
 
        display: flex;   
        flex-direction: column;
        padding: 15px 20px 20px 20px;
        margin-left: 14px;
        margin-top: 14px;
        width: 133px;
        text-decoration: none;
 
        background-color: var(--white);
        box-shadow: inset 1px 1px 16px rgba(203, 203, 203, 0.8), inset -1px -1px 16px #FFFFFF;
        border-radius: 50px 0px;
 
        opacity: 0.8;
    }
 
    &__text{
        text-decoration: none;
        text-align: center;
        font-family: var(--main-font);
        font-weight: 400;
        font-size: 14px;
        line-height: 1.28;
 
        color: var(--blue);
    }
 
        &__img{
        display: block;
        align-items: center;
        justify-content: center;
        margin-bottom: 18px;
        width: 94px;
        height: 82px;
        background-color: var(--white);
        background-size: 94px 82px;
 
        &-one{
            background-image: url(../images/scan/mobile/scan-process-1.jpg);
        }
        &-two{
            background-image: url(../images/scan/mobile/scan-process-2.jpg);
        }
        &-three{
            background-image: url(../images/scan/mobile/scan-process-3.jpg);
        }
        &-four{
            background-image: url(../images/scan/mobile/scan-process-4.jpg);
        }
 
 
    }
}
 
@media screen and (min-device-pixel-ratio: 2) and (min-width: 320px),
screen and (min-resolution: 192dpi) and (min-width:320px),
screen and (min-resolution: 2dppx) and (min-width:320px) {
    .scan-process__img{
        &-one{
            background-image:url(../images/scan/mobile/scan-process-1@2x.jpg);
        }
        &-two{
            background-image: url(../images/scan/mobile/scan-process-2@2x.jpg);
        }
        &-three{
            background-image: url(../images/scan/mobile/scan-process-3@2x.jpg);
        }
        &-four{
            background-image: url(../images/scan/mobile/scan-process-4@2x.jpg);
        }
    }
 
}
 
 
