.process-description {
  padding-bottom: 75px;
}

.process-description__item:not(:last-child) {
  padding-bottom: 50px;
}

.process-description__img-container {
  display: flex;
  min-width: 280px;
  padding-bottom: 16px;
}

.process-description__text-item:not(:last-child) {
  padding-bottom: 16px;
}

.process-description__text-list {
  padding-bottom: 25px;
}

.process-description__img {
  display: block;
  filter: drop-shadow(20px 20px 80px rgba(0, 0, 0, 0.15));
  border-radius: 50px 0px;
}

.span {
  font-weight: bold;
  font-size: 16px;
  line-height: 1.25;
  color: #487996;
}

@media screen and (min-width: 768px) {
  .process-description__item {
    display: flex;
  }

  .process-description__img-container {
    margin-right: 20px;
    padding-bottom: 0;
    min-width: 247px;
  }

  .process-description__text-list {
    padding-bottom: 50px;
  }

  .process-description__item:not(:last-child) {
    padding-bottom: 75px;
  }
}

@media screen and (min-width: 1444px) {
  .process-description__text-item {
    font-size: 18px;
    line-height: 1.27;
  }

  .process-description__item:not(:last-child) {
    padding-bottom: 100px;
  }

  .process-description__img-container {
    margin-right: 80px;
    min-width: 250px;
  }

  .process-description__text-item:not(:last-child) {
    margin-bottom: 25px;
  }
}
